.form-group {
	display: flex;
	flex-direction: column;
}

 label {
	font-weight: 600;
	max-width: 100%;
	display: block;
	margin: 0px 0px 10px 0px;
}


[type=color], [type=date], [type=datetime-local],
[type=datetime], [type=email], [type=month],
[type=number], [type=password], [type=search],
[type=tel], [type=text], [type=time],
[type=url], [type=week], select, textarea {
	display: block;
	border: 1px solid $color-form-border;
	border-radius: 0px;
	padding: .75rem;
	outline: none;
	background: transparent;
	margin-bottom: 20px;
	font-size: 1rem;
	width: 100%;
	max-width: 100%;
	line-height: 1;
	height: 45px;
	&::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
		color: $color-form-text-placeholder;
		opacity: 1; /* Firefox */
	}

	&:-ms-input-placeholder { /* Internet Explorer 10-11 */
		color: $color-form-text-placeholder;
	}

	&::-ms-input-placeholder { /* Microsoft Edge */
		color: $color-form-text-placeholder;
	}
	&.error {
		border: 1px solid $color-error;
	}
	&.success {
		border: 1px solid $color-success;
	}
 }


 select {
	-webkit-appearance: none;
	-moz-appearance: none;
	width: 100%;
	background: url("../img/formSelectArrow.png") no-repeat right;
	padding-right: 30px;
 }


// Checkbox
 label.myAccount-form-checkbox{
	display: inline;
	font-size: .875em; // (14px)
	line-height: 1.3em;
	display: block;
	position: relative;
	padding-left: 30px;
	margin-bottom: 20px;
	cursor: pointer;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;

 }


/* Hide the browser's default checkbox */
.myAccount-form-checkbox input {
	position: absolute;
	opacity: 0;
	cursor: pointer;
}

/* Create a custom checkbox */
.myAccount-checkmark {
	position: absolute;
	top: 0;
	left: 0;
	height: 20px;
	width: 20px;
	border: 1px solid $color-form-border;
}



/* When the checkbox is checked, add a blue background */
.myAccount-form-checkbox input:checked ~ .myAccount-checkmark {
	background-color: $color-button;
}

/* Create the checkmark/indicator (hidden when not checked) */
.myAccount-checkmark:after {
	content: "";
	position: absolute;
	display: none;
}

/* Show the checkmark when checked */
.myAccount-form-checkbox input:checked ~ .myAccount-checkmark:after {
	display: block;
}

/* Style the checkmark/indicator */
.myAccount-form-checkbox .myAccount-checkmark:after {
	left: 7px;
	top: 4px;
	width: 5px;
	height: 10px;
	border: solid $color-white;
	border-width: 0 3px 3px 0;
	-webkit-transform: rotate(45deg);
	-ms-transform: rotate(45deg);
	transform: rotate(45deg);
}


// password SHOW HIDE
.myAccount-password {
	display: flex;
	flex-wrap: wrap;
	&.error {
		input {
			border-right: none;
			border-color: $color-error;
			margin-bottom: 0px;
			flex: 0 0 75%;
		}
		p {
			margin-top: 20px;
			color: $color-error;
		}
	}

	&.success {
		input {
			border-right: none;
			border-color: $color-success;
			margin-bottom: 0px;
			flex: 0 0 75%;
		}
	}

	input {
		border-right: none;
		margin-bottom: 0px;
		flex: 0 0 75%;
	}
	button {
		flex: 0 0 25%;
		margin-bottom:0px;
		height: 45px;
	}
	small a, small p {
		display: inline-block;
		margin-top: 10px;
		margin-bottom: 10px;
		flex: 0 0 100%;
	}
}


