// Password Strength Meter
// --------------------------------------------------

.password-meter-container {
  text-align: center;
  margin-bottom: 20px;
  p {
    margin-bottom: 0px;
  }

}


// Password Strength Meter : Outer container
.password-meter {
	flex: 0 0 100%;
	overflow: none;
	height: 10px;
	margin-bottom: 40px;
	background-color: $color-mediumgrey;
	border-radius: none;
	box-shadow: none;
}

.password-meter-body {
	display: block;
	margin-top:5px;
}

// Password Strength Meter Bar : Progress
.password-meter-bar {
  width: 0%;
  height: 100%;
  font-size: 14px;
  color: purple;
  text-align: center;
  background-color: transparent;
  float: none;
  box-shadow: none;
  -webkit-transition: width .6s;
  transition: width .6s;
  transition-timing-function: ease;
}


// Password Strength Meter Bar : Variations
// -------------------------

.password-meter-bar-success {
  background-color: $color-green;
}

.password-meter-bar-warning {
  background-color: $color-yellow;
}

.password-meter-bar-danger {
  background-color: $color-red;
}




// password rules checkmark
ul.myAccount-password-instruction {
	list-style-type: none;
	margin:0px 0px 20px 0px;
	padding:0px;
	li {
		margin:0px 0px 5px 0px;
		display: flex;
		align-items: center;
		&.success {
			.check-mark__wrapper {
				border: 2px solid $color-green;
				.check-mark {
					background-color: $color-green;
				}
			}       
		}
		&.error {
			.check-mark__wrapper {
				border: 2px solid $color-red;
				.check-mark {
					background-color: $color-red;
				}
			}       
		}
	}
	p {
		display: inline-block;
		margin-bottom: 0px;
		font-size: 80%;
	}
}

.check-mark__wrapper {
	width: 17px;
	height: 17px;
	margin: 0px 5px 0px 0px;
	border: 2px solid $color-text;
	border-radius: 50%;
	display: inline-block;
	overflow: hidden;
}


.check-mark {
	position: relative;
	width: 5px;
	height: 9px;
	margin-left: 4px;
	margin-bottom: 0px;
	margin-top: 1px;
	background-color: $color-text;
	-webkit-transform: rotate(45deg);
	transform: rotate(45deg);
}

.check-mark::before {
	content: "";
	display: block;
	position: absolute;
	top: -1px;
	right: 2px;
	z-index: 10;
	width: 5px;
	height: 8px;
	background-color: $color-white;
}



